import axios from 'axios';
import { toast } from 'react-toastify';

import actions from './contacts-actions'; // –ò–º–ø–æ—Ä—Ç —ç–∫—à–µ–Ω–æ–≤ –∏–∑ –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤ –≤ –æ–ø–µ—Ä–∞—Ü–∏–∏

// –ê—Å–∏–Ω—Ö—Ä–æ–Ω–Ω–∞—è –æ–ø–µ—Ä–∞—Ü–∏—è –ø–æ–ª—É—á–µ–Ω–∏—è —Å–ø–∏—Å–∫–∞ –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤ (–¥–µ–ª–∞–µ—Ç –∑–∞–ø—Ä–æ—Å, –¥–∏—Å–ø–∞—Ç—á–∏—Ç —ç–∫—à–Ω –ø–æ–ª—É—á–µ–Ω–∏—è)
const fetchContacts = () => async dispatch => {
  dispatch(actions.fetchContactsRequest());

  try {
    const { data } = await axios.get('/contacts');

    dispatch(actions.fetchContactsSuccess(data));
  } catch (error) {
    dispatch(actions.fetchContactsError(error.message));
    toast.error(error.message);
  }
};

// –ê—Å–∏–Ω—Ö—Ä–æ–Ω–Ω–∞—è –æ–ø–µ—Ä–∞—Ü–∏—è –ø–æ –¥–æ–±–∞–≤–ª–µ–Ω–∏—é –∫–æ–Ω—Ç–∞–∫—Ç–∞ (–ø—Ä–∏–Ω–∏–º–∞–µ—Ç –¥–∞–Ω–Ω—ã–µ —Å —Ñ–æ—Ä–º—ã, –¥–µ–ª–∞–µ—Ç –∑–∞–ø—Ä–æ—Å, –¥–∏—Å–ø–∞—Ç—á–∏—Ç —ç–∫—à–Ω –¥–æ–±–∞–≤–ª–µ–Ω–∏—è)
const addContact = (name, number) => async dispatch => {
  const contact = { name, number };

  dispatch(actions.addContactRequest());

  try {
    const { data } = await axios.post('/contacts', contact);

    toast.success('üìó Contact has been added');
    dispatch(actions.addContactSuccess(data));
  } catch (error) {
    dispatch(actions.addContactError(error.message));
    toast.error(error.message);
  }
};

// –ê—Å–∏–Ω—Ö—Ä–æ–Ω–Ω–∞—è –æ–ø–µ—Ä–∞—Ü–∏—è —É–¥–∞–ª–µ–Ω–∏—è –∫–æ–Ω—Ç–∞–∫—Ç–∞ (–ø—Ä–∏–Ω–∏–º–∞–µ—Ç id, –¥–µ–ª–∞–µ—Ç –∑–∞–ø—Ä–æ—Å, –¥–∏—Å–ø–∞—Ç—á–∏—Ç —ç–∫—à–Ω —É–¥–∞–ª–µ–Ω–∏—è)
const deleteContact = id => async dispatch => {
  dispatch(actions.deleteContactRequest());

  try {
    await axios.delete(`/contacts/${id}`);

    toast.info('üìï Contact has been deleted');
    dispatch(actions.deleteContactSuccess(id));
  } catch (error) {
    dispatch(actions.deleteContactError(error.message));
    toast.error(error.message);
  }
};

// eslint-disable-next-line
export default { fetchContacts, addContact, deleteContact };
