import { useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { authSelectors, authOperations } from '../../redux/auth';

import styles from './UserMenu.module.scss';

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç –º–µ–Ω—é –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –ø–æ—Å–ª–µ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
export default function UserMenu() {
  //const email = useSelector(authSelectors.getUserEmail); // –°–µ–ª–µ–∫—Ç–æ—Ä –ø–æ—á—Ç—ã —é–∑–µ—Ä–∞
  const name = useSelector(authSelectors.getUserName);

  const dispatch = useDispatch();

  // –î–∏—Å–ø–∞—Ç—á–∏—Ç –æ–ø–µ—Ä–∞—Ü–∏—é –≤—ã—Ö–æ–¥–∞ –∏–∑ –ø—Ä–æ—Ñ–∏–ª—è + useCallback
  const onLogout = useCallback(() => dispatch(authOperations.logOut()), [
    dispatch,
  ]);

  return (
    <div className={styles.profile}>
      <div className={styles.thumb}>
        <img
          //src={`https://eu.ui-avatars.com/api/?background=2196f3&color=fff&&length=1&name=${email}`}
          src={`https://eu.ui-avatars.com/api/?background=2196f3&color=fff&&length=1&name=${name}`}
          alt="avatar"
          title="Your avatar"
          className={styles.avatar}
        />
      </div>

      <span className={styles.welcome}>
        {/* Welcome, <span className={styles.email}>{email}</span> */}
        Welcome, <span className={styles.name}>{name}</span>
      </span>

      <button
        type="button"
        title="Log out"
        aria-label="Log out"
        onClick={onLogout}
        className={styles.button}
      >
        Logout
      </button>
    </div>
  );
}
