import { createAction } from '@reduxjs/toolkit'; // –ò–º–ø–æ—Ä—Ç —Ñ—É–Ω–∫—Ü–∏–∏ —Å–æ–∑–¥–∞–Ω–∏—è —ç–∫—à–µ–Ω–∞ –∏–∑ —Ç—É–ª–∫–∏—Ç–∞

// –°–æ–∑–¥–∞–Ω–∏–µ —ç–∫—à–µ–Ω–æ–≤ –¥–ª—è –ø–æ–ª—É—á–∞–µ–Ω–∏—è —Å–ø–∏—Å–∫–∞ –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤
export const fetchContactsRequest = createAction(
  'contacts/fetchContactsRequest',
);
export const fetchContactsSuccess = createAction(
  'contacts/fetchContactsSuccess',
);
export const fetchContactsError = createAction('contacts/fetchContactsError');

// –°–æ–∑–¥–∞–Ω–∏–µ —ç–∫—à–µ–Ω–æ–≤ –¥–ª—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –∫–æ–Ω—Ç–∞–∫—Ç–∞
const addContactRequest = createAction('contacts/addContactsRequest');
