import { Route, Redirect } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { authSelectors } from '../redux/auth';

// - –ï—Å–ª–∏ –º–∞—Ä—à—Ä—É—Ç –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–Ω—ã–π, –∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∑–∞–ª–æ–≥–∏–Ω–µ–Ω, —Ä–µ–Ω–¥–µ—Ä–∏—Ç —Ä–µ–¥–∏—Ä–µ–∫—Ç –Ω–∞ —É–∫–∞–∑–∞–Ω–Ω—ã–π —Ä–æ—É—Ç
// - –í –ø—Ä–æ—Ç–∏–≤–Ω–æ–º —Å–ª—É—á–∞–µ —Ä–µ–Ω–¥–µ—Ä–∏—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç –ø—É–±–ª–∏—á–Ω–æ–≥–æ —Ä–æ—É—Ç–∞
export default function PublicRoute({
  redirectTo,
  children,
  ...routeProps
}) {
  const isLoggedIn = useSelector(authSelectors.getIsAuthenticated); // –°–µ–ª–µ–∫—Ç–æ—Ä —Å—Ç–∞—Ç—É—Å–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏

  return (
    <Route {...routeProps}>
      {isLoggedIn && routeProps.restricted
        ? (<Redirect to={redirectTo} />)
        : (children)
      }
    </Route>
  );
}
