import { Route, Redirect } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { authSelectors } from '../redux/auth';

// - –ï—Å–ª–∏ –º–∞—Ä—à—Ä—É—Ç –ø—Ä–∏–≤–∞—Ç–Ω—ã–π –∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∑–∞–ª–æ–≥–∏–Ω–µ–Ω, —Ä–µ–Ω–¥–µ—Ä–∏—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç
// - –í –ø—Ä–æ—Ç–∏–≤–Ω–æ–º —Å–ª—É—á–∞–µ —Ä–µ–Ω–¥–µ—Ä–∏—Ç Redirect –Ω–∞ —É–∫–∞–∑–∞–Ω–Ω—ã–π —Ä–æ—É—Ç

// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç –ø—Ä–∏–≤–∞—Ç–Ω–æ–≥–æ —Ä–æ—É—Ç–∞
export default function PrivateRoute({
  redirectTo,
  children,
  ...routeProps
}) {
  const isLoggedIn = useSelector(authSelectors.getIsAuthenticated);

  return (
    <Route {...routeProps}>
      {isLoggedIn
        ? children
        : <Redirect to={redirectTo} />}
    </Route>
  );
}
