import { configureStore, getDefaultMiddleware } from '@reduxjs/toolkit'; // –ò–º–ø–æ—Ä—Ç —Ñ—É–Ω–∫—Ü–∏–∏ —Å–æ–∑–¥–∞–Ω–∏—è —Ö—Ä–∞–Ω–∏–ª–∏—â–∞ –∏ –ø—Ä–æ—Å–ª–æ–π–∫–∏
import {
  persistStore,
  persistReducer,
  FLUSH,
  REHYDRATE,
  PAUSE,
  PERSIST,
  PURGE,
  REGISTER,
} from 'redux-persist'; // –ò–º–ø–æ—Ä—Ç —Ñ—É–Ω–∫—Ü–∏–∏ –ø–µ—Ä—Å–∏—Å—Ç–µ—Ä–æ–≤ –∏ —Ñ–∏–∫—Å–∞ –∫–æ–Ω—Å–æ–ª–∏
import storage from 'redux-persist/lib/storage'; // –ò–º–ø–æ—Ä—Ç –ª–æ–∫–∞–ª—å–Ω–æ–≥–æ —Ö—Ä–∞–Ω–∏–ª–∏—â–∞
import logger from 'redux-logger'; // –ò–º–ø–æ—Ä—Ç –ª–æ–≥–≥–µ—Ä–∞

import { authReducer } from './auth'; // –ò–º–ø–æ—Ä—Ç —Ä–µ–¥—é—Å–µ—Ä–∞ –ø–æ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
import { contactsReducer } from './contacts'; // –ò–º–ø–æ—Ä—Ç —Ä–µ–¥—é—Å–µ—Ä–∞ –ø–æ –∫–æ–Ω—Ç–∞–∫—Ç–∞–º

// –°–æ–∑–¥–∞–Ω–∏–µ –ø—Ä–æ—Å–ª–æ–µ–∫ + –ª–æ–≥–≥–µ—Ä. –í–∞–∂–µ–Ω –ø–æ—Ä—è–¥–æ–∫!
const middleware = [
  ...getDefaultMiddleware({
    serializableCheck: {
      ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
    },
  }),
];

// –î–æ–±–∞–≤–ª—è–µ—Ç –ª–æ–≥–≥–µ—Ä —Ç–æ–ª—å–∫–æ –¥–ª—è –ø—Ä–æ–¥–∞–∫—à–µ–Ω–∞
if (process.env.NODE_ENV === `development`) {
  middleware.push(logger);
}

// –ö–æ–Ω—Ñ–∏–≥ –ø–µ—Ä—Å–∏—Å—Ç–æ—Ä–∞
const authPersistConfig = {
  key: 'auth',
  storage,
  whitelist: ['token'],
};

// –°–æ–∑–¥–∞–Ω–∏–µ —Ö—Ä–∞–Ω–∏–ª–∏—â–∞ (–∫–æ—Ä–Ω–µ–≤–æ–π —Ä–µ–¥—é—Å–µ—Ä + –ø—Ä–æ—Å–ª–æ–π–∫–∏ + —Ç—É–ª–∑—ã —Ç–æ–ª—å–∫–æ –¥–ª—è —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏)
const store = configureStore({
  reducer: {
    auth: persistReducer(authPersistConfig, authReducer),
    contacts: contactsReducer,
  },
  middleware,
  devTools: process.env.NODE_ENV === 'development',
});

// –°–æ–∑–¥–∞–Ω–∏–µ –ø–µ—Ä—Å–∏—Å—Ç–æ—Ä–∞
const persistor = persistStore(store);

// eslint-disable-next-line
export default { store, persistor };
